import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import PropTypes from 'prop-types';
import React from 'react';
import { Button } from '@contentful/forma-36-react-components';

import '@contentful/forma-36-react-components/dist/styles.css';

class App extends React.Component {
    static propTypes = {
        sdk: PropTypes.object.isRequired
    };
    
    constructor(props) {
        super(props);
        this.state = {
            name: props.sdk.parameters.invocation.name,
            details: props.sdk.parameters.invocation.details,
        };
    }    

    onNameChangeHandler = (event) => {
        this.setState({ ...this.state, name: event.target.value });
    }

    onDetailsChangeHandler = (event, editor) => {
        this.setState({ ...this.state, details: editor.getData() });
    }

    onSaveClickHandler = (event) => {
        this.props.sdk.close({
            action: 'save',
            name: this.state.name,
            details: this.state.details,
        });
    }

    onCancelClickHandler = (event) => {
        this.props.sdk.close({
            action: 'cancel',
            name: this.state.name,
            details: this.state.details,
        });
    }

    componentDidMount() {
        this.props.sdk.window.startAutoResizer();
    }

    render() {
        return (
            <div className="entry">
                <div className="input-group">
                    <label>Step Name</label>
                    <input type="text" value={this.state.name} onChange={this.onNameChangeHandler} />
                </div>

                <div className="input-group">
                    <label>Step Details</label>
                    <CKEditor editor={ClassicEditor} data={this.state.details} onChange={this.onDetailsChangeHandler} />
                </div>           
                
                <div className="input-group buttons">
                    <Button buttonType="primary" onClick={this.onSaveClickHandler}>
                        Save Changes
                    </Button>

                    <Button buttonType="negative" onClick={this.onCancelClickHandler}>
                        Cancel
                    </Button>                    
                </div>
            </div>
        )
    }
}

export default App;